@import url(https://fonts.googleapis.com/css?family=Rajdhani:700);
@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
@import url(https://cdn.jsdelivr.net/gh/moonspam/NanumSquare@1.0/nanumsquare.css);
body {
  margin: 0;
	font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

header {
	width: 100%;
	height: 50px;
	z-index: 10;
	top: 0px;
	position: fixed;
	background-color: #3591fe;
	border-bottom: 1px solid rgba(150, 150, 150, 0.2);
	box-sizing: border-box;
	transition: All 0.2s ease;
	-webkit-transition: All 0.2s ease;
	-moz-transition: All 0.2s ease;
	-o-transition: All 0.2s ease;
	text-align: center;
	color: white;
	line-height: 50px;
}

#header {
	font-family: sans-serif;
	font-size: 20px;
}

#selector {
	color: black;
	font-family: sans-serif;
	font-size: 20px;
}

#top {
	background-color: #00020b;
}

.ui.inverted.menu .item:before {
	background-color: #000000;
}
.home_background {
	background-color: #222;
	text-shadow: 0 0 5px #C8C8C8;
	background-size: cover;
	height: 400px;
	color: white;
	padding: 50px
}

.home_container {
	font-family: Consolas
}

.date {
	color: #828282;
	font-size: 1rem;
}

::-moz-selection {
  background: #0080FF;
  text-shadow: none;
}

::selection {
  background: #0080FF;
  text-shadow: none;
}

#default {
	font-size: 1.2rem;
	font-family: Consolas
}

#card {
	margin: auto;
}

.description {
	font-family: "NanumSquare";
	font-size: 1.5rem;
	font-weight: normal;
}

h1 {
	font-family: Consolas
}
.blog_background {
	background-color: #222;
	text-shadow: 0 0 5px #C8C8C8;
	background-size: cover;
	height: 400px;
	color: white;
	padding: 50px
}

.blog_container {
	font-family: Consolas
}

h1 {
	font-family: Consolas
}

.ui.sticky{
	margin-top: 75px;
}
